.wrapper {
  width: 100%;
  height: 42px;
  background-color: var(--primary-color);
  color: #fff;
}
.inner {
  width: 100%;
  max-width: 1200px;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: auto;
}
.category {
  width: 280px;
  font-size: 1.4rem;
  cursor: pointer;
  position: relative;
  display: flex;
  align-items: center;

  &::after {
    content: "";
    width: 1px;
    height: 20px;
    background-color: #fff;
    color: green;
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
  }
}

.category-text {
  font-weight: bold;
  font-size: 1.5rem;
  margin-left: 7px;
}

// categ
.category-list {
  width: 280px;
  background-color: #fff;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  border-radius: 3px;
  animation: showList ease 0.3s;
}

@keyframes showList {
  from {
    transform: scale(0);
    transform-origin: top left;
    opacity: 0;
  }
  to {
    transform: scale(1);
    transform-origin: top left;
    opacity: 1;
  }
}

.category-item {
  display: flex;
  align-items: center;
  padding: 10px;
  transition: all ease 0.1s;
  cursor: pointer;

  &:hover {
    background-color: #ffb416;
  }
}

.category-img {
  width: 25x;
  height: 25px;
  object-fit: cover;
}
.category-name {
  font-size: 1.4rem;
  color: #323c42;
  font-weight: 400;
  margin-left: 6px;
}

.nav-list {
  flex: 1;
  width: 100%;
  height: 100%;
  list-style: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-left: 15px;
}

.nav-item {
  width: 150px;
  height: 100%;
  font-size: 1.5rem;
  line-height: 42px;
  text-align: center;
  transition: all ease 0.2s;
  cursor: pointer;

  &:hover {
    color: #ffb416;
  }

  .icon-arrow-down {
    position: relative;
    top: 3px;
  }
}

@media (min-width: 740px) and (max-width: 1023px) {
  .category {
    width: 200px;
    margin-left: 44px;
  }

  .category-list {
    width: 200px;
  }
  .nav-list {
    justify-content: center;
    margin-right: 30px;
  }

  .nav-item {
    width: 90px;
  }
}

// mobile
@media only screen and (max-width: 739px) {
  .wrapper {
    display: none;
  }
}
