// search
.search {
  width: 338px;
  height: 40px;
  border: solid 1px var(--primary-color);
  border-radius: 5px;
  overflow: hidden;
  position: relative;

  input {
    width: 293px;
    height: 100%;
    outline: none;
    border: none;
    padding: 0 10px;
    font-size: 1.5rem;
  }
}
.search-icon-mobile {
  display: none;
}

.search-icon {
  height: 100%;
  width: 43px;
  font-size: 24px;
  color: rgb(61, 59, 59);
  position: absolute;
  right: 0;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  transition: all linear 0.1s;
  cursor: pointer;

  &:hover {
    background: var(--primary-color);
    color: #fff;
  }
}

.search-result {
  width: 338px;
  background-color: #fff;
  border-radius: 5px;
  overflow: hidden;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
}
.search-text {
  font-size: 1.4rem;
  color: #323c42;
  height: 34px;
  display: block;
  border-bottom: 1px solid #ccc;
  background: #f5f5f5;
  line-height: 34px;
  padding-left: 10px;
}

.search-list {
  height: 100%;
  max-height: 400px;
  overflow-y: auto;
  overflow-x: hidden;

  &::-webkit-scrollbar {
    border-radius: 0;
    width: 8px;
  }

  &::-webkit-scrollbar-thumb {
    border-radius: 4px;
    background-color: rgb(0, 0, 0, 0.2);
  }

  &::-webkit-scrollbar-track {
    border-radius: 0;
    background-color: rgba(0, 0, 0, 0);
  }
}
.search-item {
  display: flex;
  align-items: center;
  padding: 10px 6px;
  border-bottom: 1px solid #ccc;

  &:hover {
    background-color: #f1f1f1;
    cursor: pointer;
  }
}
.search-item-img {
  width: 42px;
  height: 42px;
  margin-left: 3px;
  object-fit: contain;
}
.search-item-infos {
  flex: 1;
  margin-left: 10px;
}
.search-item-name {
  width: 100%;
  max-width: 270px;
  color: #333;
  font-size: 1.4rem;
  font-weight: 700;
  overflow: hidden;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
}
.search-item-price {
  font-size: 1.2rem;
  color: #c70100;
}
.btn-view-all {
  font-size: 1.4rem;
  text-align: center;
  height: 34px;
  line-height: 34px;
  width: 100%;
  color: var(--primary-color);
  background: none;
  border: none;
  display: block;
  cursor: pointer;

  &:hover {
    background-color: var(--primary-color);
    color: #fff;
  }
}

@media (max-width: 739px) {
  .search {
    display: none;

    input {
      width: 80%;
    }

    &.active {
      display: block;
      position: fixed;
      top: -50px;
      width: 95%;
      opacity: 0;
      margin: auto;
      background-color: #fff;
      animation: openSearch ease 0.3s forwards;
    }
  }
  .search-icon {
    background-color: var(--primary-color);
    color: #fff;
  }

  @keyframes openSearch {
    to {
      transform: translateY(108px);
      opacity: 1;
    }
  }

  .search-result {
    width: 100%;
  }
}

