.wrapper {
  padding: 30px 0 60px 0;
  margin-bottom: 60px;
  min-height: 300px;
}

.heading {
  font-size: 3rem;
  font-weight: 400;
  color: #22292e;
  text-align: center;
}

.count {
  font-size: 1.5rem;
  text-align: center;
  display: block;
  margin-top: 10px;
}

.product-and-checkout {
  display: flex;
  justify-content: space-between;
  margin-top: 30px;
}

.product-list {
  width: 800px;
  border-top: 1px solid #e9e6e6;
  border-bottom: 1px solid #e9e6e6;
  max-height: 470px;
  overflow-y: auto;

  &::-webkit-scrollbar {
    border-radius: 0;
    width: 8px;
  }

  &::-webkit-scrollbar-thumb {
    border-radius: 4px;
    background-color: rgb(0, 0, 0, 0.2);
  }

  &::-webkit-scrollbar-track {
    border-radius: 0;
    background-color: rgba(0, 0, 0, 0);
  }
}

.product-item {
  display: flex;
  padding: 30px 15px;
  border-bottom: 1px solid #e9e6e6;
  position: relative;

  &:last-child {
    border-bottom: none;
  }

  img {
    width: 80px;
    height: 80px;
    object-fit: contain;
  }
}

.product-infos {
  flex: 1;
  margin-left: 20px;
}

.name {
  font-size: 1.6rem;
  font-weight: bold;
  color: #323c42;
  display: inline-block;
  cursor: pointer;

  &:hover {
    color: #ffb416;
  }
}

.price {
  font-size: 1.4rem;
  margin-top: 20px;
  display: block;
}

.quantity-and-total {
  display: flex;
  justify-content: space-between;
  margin-top: 15px;
}

.quantity {
  display: flex;
}

.increase,
.decrease {
  width: 25px;
  height: 25px;
  font-weight: 500;
  color: #333;
  background-color: #fff;
  border: 1px solid #ccc;
  cursor: pointer;
}

.increase {
  border-left: none;
}
.decrease {
  border-right: none;
}

.number {
  width: 35px;
  height: 25px;
  font-size: 1.5rem;
  border: 1px solid #ccc;
  background-color: rgb(241, 238, 238);
  text-align: center;
  line-height: 23px;
  display: block;
}

.total-price {
  font-size: 1.6rem;
  font-weight: 600;
}

.close {
  font-size: 1.9rem;
  position: absolute;
  right: 14px;
  top: 11px;
  padding: 6px;
  color: #333;
  cursor: pointer;
}

.checkout {
  width: 90%;
  max-width: 400px;
  height: 296px;
  border: 1px solid #e1e3e4;
  padding: 16px;
  margin-left: 20px;
}

.checkout-info {
  font-size: 2.8rem;
  font-weight: 400;
  color: #323c42;
  padding-bottom: 14px;
  border-bottom: 1px solid #f1f1f1;
}

.checkout-total {
  height: 59px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #f1f1f1;
}

.checkout-total-text {
  font-size: 1.6rem;
  font-weight: 600;
  color: #434d53;
}

.checkout-total-price {
  font-size: 2.4rem;
  font-weight: bold;
  color: red;
}

.checkout-text {
  margin-top: 15px;
  font-size: 1.5rem;
  color: #323c42;
  line-height: 22px;
}

.checkout-btn {
  width: 100%;
  height: 41px;
  font-size: 1.6rem;
  color: #fff;
  border: none;
  background-color: #e00707;
  margin-top: 15px;
  border-radius: 5px;
  cursor: pointer;

  &:hover {
    filter: brightness(90%);
  }
}

.keep-buying {
  margin-top: 20px;
  text-align: center;
  font-size: 1.5rem;
  color: #333;
  display: flex;
  justify-content: center;
  cursor: pointer;

  &:hover {
    color: #ffb416;
  }

  .back-icon {
    font-size: 1.8rem;
    margin-right: 3px;
  }
}

@media (min-width: 740px) and (max-width: 1023px) {
  .product-list {
    width: 900px;
  }
  .name {
    width: 200px;
  }
  .checkout {
    height: 317px;
  }
}
@media (max-width: 739px) {
  .wrapper {
    padding: 0;
  }

  .heading,
  .checkout-info {
    font-size: 2rem;
  }

  .product-and-checkout {
    flex-direction: column;
  }

  .product-list {
    width: 100%;
  }
  .product-item {
    padding: 20px 0;
  }
  .name {
    width: 226px;
  }

  .price {
    font-size: 1.5rem;
  }
  .total-price {
    margin-right: 10px;
  }

  .checkout {
    width: 100%;
    margin: 20px 0 0 0;
  }
}
